#img-logo,
#img-logo-filled {
  margin-left: 100px;
  height: 60px;
  width: 250px;
  transition: all 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

#img-logo-filled {
  position: absolute;
  transform: translateX(-350px);
  opacity: 0;
  z-index: -1;
}
#img-logo:hover {
  opacity: 0;
}
#img-logo:hover + #img-logo-filled {
  opacity: 1;
}

#container {
  position: fixed;
  width: 100vw;
  max-width: 100%;
  background-color: rgba(255, 255, 255, 0);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 50px 0px;
  z-index: 100;
}

.hamburger {
  margin-right: 100px;
  display: flex;
  align-self: center;
  flex-direction: column;
  justify-content: space-between;
  width: 50px;
  height: 30px;
  cursor: pointer;
}
.hamburger div {
  border-radius: 5px;
  align-self: flex-end;
  height: 6px;
  width: 100%;
  background: #fff;
}
.hamburger .meat {
  width: 75%;
  transition: all 400ms ease-in-out;
}
.hamburger .bottom-bun {
  width: 50%;
  transition: all 800ms ease-in-out;
}
.hamburger:hover div {
  width: 100%;
}
.hamburger:hover .top-bun {
  animation: burger-hover 1s ease-in-out alternate;
}
.hamburger:hover .meat {
  animation: burger-hover 1s ease-in-out alternate forwards 400ms;
}
.hamburger:hover .bottom-bun {
  animation: burger-hover 1s ease-in-out alternate forwards 800ms;
}

@keyframes burger-hover {
  0% {
    width: 100%;
  }
  50% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}

.hamburger.menu-opened {
  display: flex;
  align-self: center;
  flex-direction: column;
  justify-content: center;
  width: 50px;
  height: 30px;
  cursor: pointer;
}
.hamburger.menu-opened div {
  border-radius: 5px;
  height: 6px;
  background: #fff;
}

@keyframes openMenuTop {
  0% {
    transform: translateY(16px);
  }
  100% {
    transform: rotate(45deg) translateY(0px);
  }
}

@keyframes openMenuBot {
  0% {
    transform: translateY(-16px);
  }
  100% {
    transform: rotate(-45deg) translateY(0px);
  }
}

.hamburger.menu-opened .top-bun {
  width: 50px;
  position: absolute;
  animation: openMenuTop 1s ease-in-out;
  transform: rotate(45deg) translateY(0px);
  transition: none;
}

.hamburger.menu-opened .meat {
  animation: none;
  transition: all 1000ms ease-in-out;
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}
.hamburger.menu-opened .bottom-bun {
  width: 50px;
  position: absolute;
  animation: openMenuBot 1s ease-in-out;
  transform: rotate(-45deg) translateY(0px);
  transition: none;
}
