@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400&family=Roboto:wght@500&display=swap');

body {
  scroll-behavior: smooth;
}

#pages-container {
  width: 300vw;
  display: flex;
  flex-direction: row;
  z-index: -1;
}

#pages-container section {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#pages-container section:nth-child(1) {
  background-color: #64728b;
}

#main-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

#developper,
#investment_dashboard {
  width: 700px;
}

#text-container {
  color: rgb(255, 255, 255);
  width: 400px;
  margin-right: 50px;
  margin-left: 50px;
}

#text-container p {
  font-size: 20px;
  font-family: 'Quicksand', sans-serif;
}

#text-container h1 {
  line-height: 100%;
  margin: 0 0 20px;
  font-size: 64px;
  letter-spacing: -1px;
  font-family: 'Roboto', sans-serif;
}

#pages-container section:nth-child(2) {
  background-color: rgb(94, 156, 146);
}

#pages-container section:nth-child(3) {
  background-color: rgb(169, 170, 113);
}

::-webkit-scrollbar {
  display: none;
}

/**DEV_ANIMATIONS**/

#react {
  animation: resize 3s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

#python {
  animation: resize 2s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

#js {
  animation: resize 2.8s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

#html {
  animation: resize 2s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

#css {
  animation: resize 2.5s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes resize {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

#vapor1 {
  animation: resize_vapor 10s infinite linear;
  transform-box: fill-box;
  animation-delay: 0s;
}

#vapor2 {
  animation: resize_vapor 10s infinite linear;
  transform-box: fill-box;
  animation-delay: 0s;
}

#vapor3 {
  animation: resize_vapor 10s infinite linear;
  transform-box: fill-box;
  animation-delay: 0s;
}

@keyframes resize_vapor {
  0% {
    transform: scale(0.2);
    transform-origin: bottom;
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.2);
    transform-origin: top;
  }
}

#light {
  animation: turn_on_off 5s infinite linear;
}

@keyframes turn_on_off {
  10% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
}

/**INVESTOR_ANIMATIONS**/

#money_plant {
  animation: resize_plant 10s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
}

@keyframes resize_plant {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.8);
  }
  60% {
    transform: scale(1.8);
  }
  100% {
    transform: scale(1);
  }
}

#gold_coin {
  animation: rotate_coin 10s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes rotate_coin {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

#candle {
  animation: resize_candle 5s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes resize_candle {
  0% {
    transform: scale(1);
  }
  15% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.6);
  }
  60% {
    transform: scale(0.6);
  }
  85% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

#bar_1 {
  animation: resize_bar 5s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
  animation-delay: 2s;
}

#bar_2 {
  animation: resize_bar 5s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
}

#bar_3 {
  animation: resize_bar 5s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
  animation-delay: 4s;
}

#bar_4 {
  animation: resize_bar 5s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
  animation-delay: 2.5s;
}

@keyframes resize_bar {
  0% {
    transform: scaleY(0.2);
  }
  50% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0.2);
  }
}

#moving_pie_chart {
  animation: rotate_pie_chart 5s infinite linear;
  transform-origin: top;
  transform-box: fill-box;
}

@keyframes rotate_pie_chart {
  0% {
    transform: rotateZ(0);
  }
  40% {
    transform: rotateZ(-15deg);
  }
  80% {
    transform: rotateZ(0);
  }
}
