  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 50px 0px;
  z-index: 100;
}

.hamburger {
  margin-right: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50px;
  height: 30px;
  cursor: pointer;
}
.hamburger div {
  border-radius: 5px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  height: 6px;
  width: 100%;
  background: #fff;
}
.hamburger .meat {
  width: 75%;
  transition: all 400ms ease-in-out;
}
.hamburger .bottom-bun {
  width: 50%;
  transition: all 800ms ease-in-out;
}
.hamburger:hover div {
  width: 100%;
}
.hamburger:hover .top-bun {
  -webkit-animation: burger-hover 1s ease-in-out alternate;
          animation: burger-hover 1s ease-in-out alternate;
}
.hamburger:hover .meat {
  -webkit-animation: burger-hover 1s ease-in-out alternate forwards 400ms;
          animation: burger-hover 1s ease-in-out alternate forwards 400ms;
}
.hamburger:hover .bottom-bun {
  -webkit-animation: burger-hover 1s ease-in-out alternate forwards 800ms;
          animation: burger-hover 1s ease-in-out alternate forwards 800ms;
}

@-webkit-keyframes burger-hover {
  0% {
    width: 100%;
  }
  50% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}

@keyframes burger-hover {
  0% {
    width: 100%;
  }
  50% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}

.hamburger.menu-opened {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50px;
  height: 30px;
  cursor: pointer;
}
.hamburger.menu-opened div {
  border-radius: 5px;
  height: 6px;
  background: #fff;
}

@-webkit-keyframes openMenuTop {
  0% {
    -webkit-transform: translateY(16px);
            transform: translateY(16px);
  }
  100% {
    -webkit-transform: rotate(45deg) translateY(0px);
            transform: rotate(45deg) translateY(0px);
  }
}

@keyframes openMenuTop {
  0% {
    -webkit-transform: translateY(16px);
            transform: translateY(16px);
  }
  100% {
    -webkit-transform: rotate(45deg) translateY(0px);
            transform: rotate(45deg) translateY(0px);
  }
}

@-webkit-keyframes openMenuBot {
  0% {
    -webkit-transform: translateY(-16px);
            transform: translateY(-16px);
  }
  100% {
    -webkit-transform: rotate(-45deg) translateY(0px);
            transform: rotate(-45deg) translateY(0px);
  }
}

@keyframes openMenuBot {
  0% {
    -webkit-transform: translateY(-16px);
            transform: translateY(-16px);
  }
  100% {
    -webkit-transform: rotate(-45deg) translateY(0px);
            transform: rotate(-45deg) translateY(0px);
  }
}

.hamburger.menu-opened .top-bun {
  width: 50px;
  position: absolute;
  -webkit-animation: openMenuTop 1s ease-in-out;
          animation: openMenuTop 1s ease-in-out;
  -webkit-transform: rotate(45deg) translateY(0px);
          transform: rotate(45deg) translateY(0px);
  transition: none;
}

.hamburger.menu-opened .meat {
  -webkit-animation: none;
          animation: none;
  transition: all 1000ms ease-in-out;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}
.hamburger.menu-opened .bottom-bun {
  width: 50px;
  position: absolute;
  -webkit-animation: openMenuBot 1s ease-in-out;
          animation: openMenuBot 1s ease-in-out;
  -webkit-transform: rotate(-45deg) translateY(0px);
          transform: rotate(-45deg) translateY(0px);
  transition: none;
}

