#root {
  background-color: #64728b;
}

#profile-container hr {
  margin: 30px 0px;
  border-top: 1px solid rgb(246, 253, 255);
  opacity: 0.5;
  width: 100%;
}

#contact-wrapper {
  display: flex;
  align-items: center;
}

#about_me {
  background-color: rgb(246, 253, 255);
  border-radius: 10px;
}

#about_me p {
  margin: 3px 5px;
}

#profile-container {
  padding-top: 20px;
  margin: 0px 20vw 0 20vw;
  display: flex;

  flex-direction: column;
  align-items: center;
}

#profile-container h1 {
  color: rgb(246, 253, 255);
  font-size: 50px;
}

#profile-container h3 {
  color: rgb(246, 253, 255);
  align-self: flex-start;
  margin-bottom: 20px;
}

#head-p {
  color: rgb(246, 253, 255);
  font-size: 22px;
}

#contact-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
}

#contact-box {
  display: flex;
  margin: 0 15px;
}

#contact-box p {
  color: rgb(246, 253, 255);
  white-space: nowrap;
}

#contact-box svg {
  position: relative;
  top: 4px;
  right: 4px;
}

#experience-row {
  padding: 5px;
  background-color: rgb(246, 253, 255);
  border-radius: 10px;
  margin-bottom: 50px;
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

/* #profile-container div:nth-of-type(4),
#profile-container div:nth-of-type(5) {
  margin-bottom: 0px;
} */

#experience-row #item {
  margin-right: 10vw;
}

#item h4 {
  width: 200px;
}

#explanation strong {
  display: block;
  margin-top: 3px;
}

#explanation p {
  display: block;
  margin-top: 12px;
}

#explanation svg {
  color: rgb(48, 155, 226);
}
