#progressbar-container {
  margin: 0;
  top: 85px;
  left: 50%;
  transform: translateX(-50%);
  position: fixed;
  display: flex;
  justify-content: center;
}

/*SVG Animation*/

#boat {
  transform: scaleX(-1);
  position: fixed;
  width: 120px;
  z-index: 2;
  animation: boat_shake 3s infinite linear;
  transition: all 1s;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes boat_shake {
  0% {
    transform: scaleX(-1) rotateZ(0deg);
  }
  25% {
    transform: scaleX(-1) rotateZ(1deg);
  }
  75% {
    transform: scaleX(-1) rotateZ(-1deg);
  }
  100% {
    transform: scaleX(-1) rotateZ(0deg);
  }
}

#boat.come {
  animation: boat_shake_come 3s infinite linear;
}

@keyframes boat_shake_come {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
    transform: rotateZ(1deg);
  }
  75% {
    transform: rotateZ(-1deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

#man_stand {
  width: 90%;
  position: relative;
  right: 70px;
  bottom: 150px;
}

#man_stand {
  opacity: 0;
  animation: man_shake 3s infinite linear;
  transform-origin: right 40%;
  transform-box: fill-box;
  transition: all 1s;
  transition-delay: 200ms;
}

#man_stand.active {
  opacity: 1;
}

#man_sit {
  width: 90%;
  position: relative;
  right: 210px;
  bottom: 130px;
  opacity: 0;
  transition: all 1s;
  transition-delay: 200ms;
}

#man_sit.active {
  opacity: 1;
}

#man_sit {
  animation: man_shake_go 3s infinite linear;
  transform-origin: right 80%;
  transform-box: fill-box;
}
@keyframes man_shake {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
    transform: rotateZ(1.5deg);
  }
  75% {
    transform: rotateZ(-1.5deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

@keyframes man_shake_go {
  0% {
    transform: scaleX(-1) rotateZ(0deg);
  }
  25% {
    transform: scaleX(-1) rotateZ(1.5deg);
  }
  75% {
    transform: scaleX(-1) rotateZ(-1.5deg);
  }
  100% {
    transform: scaleX(-1) rotateZ(0deg);
  }
}

#line_1 {
  transform-origin: top right;
  transform-box: fill-box;
  transform: scale(0.3);
}

@keyframes fishing_rod {
  0% {
    transform: scale(0.3);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.3);
  }
}

