#navbarside-container {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
  background-color: rgb(168, 161, 127);
  transition: opacity 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94),
    visibility 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  z-index: 10;
}
